import React from "react";
import {
  Grid,
  Typography,
  Card,
  CardContent,
  CardActions,
  Button,
  Link,
  OutlinedInput,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Divider
} from "@material-ui/core";
import { GitHub, Instagram } from "@material-ui/icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHackerrank } from "@fortawesome/free-brands-svg-icons";
import "../styles/dashboard.css";
import me from "../images/me.jpg";
import coding from "../images/coding.jpg";

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      learn_more: false,
      learn_more_link: null,
      learn_more_header: null,
      learn_more_desc: null,
      learn_more_lang: null,
      learn_more_topic: null,
      learn_more_sig: null
    };
  }

  handleOpenDialog(data) {
    this.setState({
      learn_more: true,
      learn_more_link: data.link,
      learn_more_header: data.header,
      learn_more_desc: data.desc2,
      learn_more_lang: data.lang,
      learn_more_sig: data.sig,
      learn_more_topic: data.topic
    });
  }

  handleDialogClose() {
    this.setState({
      learn_more: false
    });
  }

  render() {
    return (
      <div className="page">
        <div className="home" id="home">
          <Grid container className="homepage-wall">
            <Grid item sm={6} md={6} className="homepage-wall-avatar">
              <div className="center">
                <img src={me} className="avatar"></img>
              </div>
            </Grid>
            <Grid item xs={12} sm={6} md={6} className="homepage-wall-header">
              <Grid container>
                <Grid
                  item
                  xs={12}
                  className="homepage-header-head"
                  style={{ marginLeft: 20 }}
                >
                  <div className="center">
                    <img src={me} className="avatar2"></img>
                  </div>
                  Hi! I'm <b style={{ color: "yellow" }}>Soujanna</b> Dutta
                </Grid>
                <Grid
                  item
                  xs={12}
                  className="homepage-header-subhead"
                  style={{ marginLeft: 20 }}
                >
                  A Software Developer, Competetive Coder, Full Stack Dev
                  Developer, Data Analyst
                </Grid>
                <Grid
                  item
                  xs={12}
                  style={{
                    marginLeft: 20
                  }}
                >
                  <Link
                    href="https://www.github.com/soun059"
                    className="site-links"
                  >
                    <GitHub></GitHub>
                  </Link>
                  {"    "}
                  <Link
                    href="https://www.instagram.com/soun059/"
                    className="site-links"
                  >
                    <Instagram></Instagram>
                  </Link>
                  <Link
                    href="https://www.hackerrank.com/soujanna2015new1"
                    className="site-links"
                  >
                    <FontAwesomeIcon
                      icon={faHackerrank}
                      size="2x"
                    ></FontAwesomeIcon>
                  </Link>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
        <div className="myprojects" id="myprojects">
          <Grid container className="project-items">
            <Grid item xs={12} className="projects-header">
              My Projects
            </Grid>
            {[
              {
                topic: "Neural Networks",
                header: "Own Neural Net",
                lang: "Python",
                desc:
                  "A working example on Neural_net ( An idea got from playground Tensorflow)",
                desc2:
                  "A working example on Neural_net ( An idea got from playground Tensorflow) Tensor Flow Playground is a awesome example of how neural network works and Thus its one of the must-visit places if you want to learn about Neural-Net Now , I have created A neural net example working on a dataset inspired from TensorFlow Playground and the fine part is, IT ONLY USES NUMPY, MATPLOTLIB but no ml-tools like sklearn or tensorflow.Hope it will help you to understand about the neural-net.",
                link: "https://github.com/soun059/Neural_Net",
                sig: "discovering"
              },
              {
                topic: "Software",
                header: "File-Type-Searcher",
                lang: "C++",
                desc:
                  "An efficient C++ file type searcher which searches and also has the ability to open the file",
                desc2:
                  "An efficient C++ file type searcher which searches and also has the ability to open the file..... Provided the open option right now works in Windows,Libraries used : 1.Filesystem,2.string,3.Iostream ",
                link: "https://github.com/soun059/File-Type-Searcher",
                sig: "discovering"
              },
              {
                topic: "web dev",
                header: "Project 1",
                lang: "HTML/CSS/JS",
                desc: "A project on something",
                link: "www.google.com",
                sig: "minor"
              }
            ].map(obj => (
              <Grid item className="project-card">
                <Card style={{ maxWidth: 245 }} className="cards" elevation="0">
                  <CardContent>
                    <Typography
                      style={{ fontSize: 14 }}
                      color="textSecondary"
                      gutterBottom
                    >
                      {obj.topic}
                    </Typography>
                    <Typography variant="h5" component="h2">
                      {obj.header}
                    </Typography>
                    <Typography
                      style={{ marginBottom: 12 }}
                      color="textSecondary"
                    >
                      {obj.lang}
                    </Typography>
                    <Typography variant="body2" component="p">
                      {obj.desc}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button
                      size="small"
                      onClick={this.handleOpenDialog.bind(this, obj)}
                    >
                      Learn More
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
          {this.state.learn_more && (
            <Dialog
              onClose={this.handleDialogClose.bind(this)}
              aria-labelledby="customized-dialog-title"
              open={this.state.learn_more}
            >
              <DialogTitle
                id="customized-dialog-title"
                onClose={this.handleDialogClose.bind(this)}
              >
                <Typography style={{ fontWeight: "bolder" }} variant="h5">
                  {this.state.learn_more_header}
                </Typography>
              </DialogTitle>
              <DialogContent dividers>
                <Typography gutterBottom style={{ padding: 5 }}>
                  Topic : {this.state.learn_more_topic}
                </Typography>
                <Divider></Divider>
                <Typography gutterBottom style={{ padding: 5 }}>
                  Lang : {this.state.learn_more_lang}
                </Typography>
                <Divider></Divider>
                <Typography gutterBottom style={{ padding: 5 }}>
                  Significance : {this.state.learn_more_sig}
                </Typography>
                <Divider></Divider>
                <Typography gutterBottom style={{ padding: 5 }}>
                  {this.state.learn_more_desc}
                </Typography>
              </DialogContent>
              <DialogActions>
                {this.state.learn_more_link != null && (
                  <Link
                    href={this.state.learn_more_link}
                    className="link-dialog"
                  >
                    <GitHub></GitHub>
                  </Link>
                )}
                <Button
                  autoFocus
                  onClick={this.handleDialogClose.bind(this)}
                  color="primary"
                >
                  Close
                </Button>
              </DialogActions>
            </Dialog>
          )}
          <div id="works" style={{ paddingTop: 70 }}></div>
        </div>
        <div className="works">
          <Grid container className="works-items">
            <Grid item xs={12} className="works-header">
              Currently Working
            </Grid>
            {[
              {
                topic: "web dev",
                header: "Project 1",
                lang: "HTML/CSS/JS",
                desc: "A project on something",
                link: null,
                sig: "minor"
              },
              {
                topic: "web dev",
                header: "Project 1",
                lang: "HTML/CSS/JS",
                desc: "A project on something",
                link: null,
                sig: "minor"
              }
            ].map(obj => (
              <Grid item className="project-card">
                <Card style={{ width: 300 }} className="cards">
                  <CardContent>
                    <Typography
                      style={{ fontSize: 14 }}
                      color="textSecondary"
                      gutterBottom
                    >
                      {obj.origin}
                    </Typography>
                    <Typography variant="h5" component="h2">
                      {obj.header}
                    </Typography>
                    <Typography
                      style={{ marginBottom: 12 }}
                      color="textSecondary"
                    >
                      {obj.lang}
                    </Typography>
                    <Typography variant="body2" component="p">
                      {obj.desc}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button
                      size="small"
                      onClick={this.handleOpenDialog.bind(this, obj)}
                    >
                      Learn More
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
          <div id="about" style={{ paddingTop: 70 }}></div>
        </div>
        <div className="about" id="about">
          <Grid container>
            <Grid item xs={12} sm={12} md={6} className="about-images">
              <img src={coding} className="about-image"></img>
            </Grid>
            <Grid item container xs={12} sm={12} md={6} className="about-info">
              <Grid item xs={12}>
                <Typography
                  variant="h9"
                  style={{ fontSize: 14 }}
                  color="textSecondary"
                >
                  Welcome
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h4" component="h1">
                  About Me!
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h6" color="textSecondary">
                  I am a fast learning, Learning my entire life about various
                  things and gained a vast amount of knowledge. I like to play
                  games, read books, watch anime, series and most of all,
                  coding.
                  <br></br>I am a Learning Full Stack Dev Developer, One of a
                  kind
                  <br></br>I am a Learning Flutter App developer
                  <br></br>I am an adept in the usefulness of python software
                  dev
                  <br></br>I am a competetive coder with best hackerrank badges
                  and daily codechef participations
                  <br></br>I also engage myself in Data Analytics and in the
                  areas of Artificial Intelligence!
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </div>
        <div className="contact" id="contact">
          <Typography variant="h5">Contact Me!</Typography>
          <Typography variant="h7">
            Enter Your Mail to subscribe to my newsletter! and I will contact
            you personally
          </Typography>
          <br></br>
          <br></br>
          <OutlinedInput
            type="email"
            variant="outlined"
            placeholder="Email"
            style={{ color: "white" }}
          ></OutlinedInput>
          <br></br>
          <br></br>
          <Button variant="outlined" color="primary">
            Submit
          </Button>
        </div>
        <div className="footer">2019&copy; Created by Soujanna Dutta</div>
      </div>
    );
  }
}

export default Dashboard;
