import React from "react";
import "../styles/drawertogglebutton.css";
import MenuIcon from "@material-ui/icons/Menu";
import { Drawer } from "@material-ui/core";
import { List, ListItem, ListItemText, Link } from "@material-ui/core";
import "../styles/sideDrawer.css";
class DrawerToggleButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  handleChange() {
    this.setState({
      open: !this.state.open
    });
  }
  render() {
    return (
      <div>
        <button
          className="toggle-button"
          onClick={this.handleChange.bind(this)}
        >
          <MenuIcon></MenuIcon>
        </button>
        <Drawer
          open={this.state.open}
          anchor="right"
          onClose={this.handleChange.bind(this)}
          style={{
            width: 200
          }}
          className="drawer"
        >
          <List className="drawer-list">
            {[
              { name: "Home", link: "#home" },
              { name: "My Projects", link: "#myprojects" },
              { name: "Currently Working", link: "#works" },
              { name: "About Me", link: "#about" },
              { name: "Contact Me!", link: "#contact" }
            ].map(text => (
              <ListItem className="drawer-list-items">
                <Link href={text.link} className="drawer-links">
                  <ListItemText
                    primary={text.name}
                    className="drawer-link-text"
                    onClick={this.handleChange.bind(this)}
                  />
                </Link>
              </ListItem>
            ))}
          </List>
        </Drawer>
      </div>
    );
  }
}

export default DrawerToggleButton;
