import React from "react";
import "../styles/navbar.css";
import DrawerToggleButton from "./drawerToggle";

import logo from "../images/logo.png";
class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      trans: true
    };
  }
  handleScroll = () => {
    if (window.scrollY > 70)
      this.setState({
        trans: false
      });
    else {
      this.setState({
        trans: true
      });
    }
  };
  componentDidMount() {
    document.addEventListener("scroll", this.handleScroll.bind(this));
  }
  handleDivMove(e) {
    var url = e.target.href;
    var hash = url.substring(url.indexOf("#") + 1);
    var pos = document.getElementById(hash);
    window.scrollTo(0, pos.OffsetTop);
    return false;
  }

  render() {
    return (
      <header
        className="navbar"
        style={{
          backgroundColor: !this.state.trans ? "white" : "transparent",
          transition: "1s ease"
        }}
      >
        <nav className="navbar-nav">
          <div className="navbar-logo">
            <img src={logo}></img>
          </div>
          <div className="spacer"></div>
          <div className="navbar-links">
            <ul>
              <li>
                <a
                  href="#home"
                  onClick={this.handleDivMove.bind(this)}
                  id="home#"
                >
                  Home
                </a>
              </li>
              <li>
                <a
                  href="#myprojects"
                  onClick={this.handleDivMove.bind(this)}
                  id="myprojects#"
                >
                  My Projects
                </a>
              </li>
              <li>
                <a
                  href="#works"
                  onClick={this.handleDivMove.bind(this)}
                  id="works#"
                >
                  Currently Working
                </a>
              </li>
              <li>
                <a
                  href="#about"
                  onClick={this.handleDivMove.bind(this)}
                  id="about#"
                >
                  About Me
                </a>
              </li>
              <li>
                <a
                  href="#contact"
                  onClick={this.handleDivMove.bind(this)}
                  id="contact#"
                >
                  Contact me!
                </a>
              </li>
            </ul>
          </div>
          <div>
            <DrawerToggleButton></DrawerToggleButton>
          </div>
        </nav>
      </header>
    );
  }
}

export default Navbar;
